import React from 'react';
import Animal from './Animal';

class Zoo extends React.Component {

    constructor() {
        super();
        this.AnimalZoo = [
            { animalName: "Bogdan", animalKind: "Elephant", animalPicture: "https://png.pngtree.com/png-clipart/20201020/ourmid/pngtree-hand-drawn-cartoon-line-elephant-clipart-png-image_2370081.jpg" },
            { animalName: "Kesha", animalKind: "Parrot", animalPicture: "https://i.pinimg.com/originals/f8/d9/13/f8d9138e1c9021002b55a9b692880d00.png" },
            { animalName: "Misha", animalKind: "Bear", animalPicture: "https://i.pinimg.com/originals/e1/3d/31/e13d31382a9f377aa74740b448fb8894.png" }
        ]
    }

    //обязательная функция
    render() {
        return (
            <Animal animal = {this.AnimalZoo}/>
        )
    }
}

export default Zoo;