function ShowArrayAsList(props) {
    let arrayToDisplay = props.arrayToDisplay;
    return (
        <ul>
            {
                arrayToDisplay.map((item) => {
                    return (<li>{item.animalName} - {item.animalKind} - <img src={item.animalPicture} alt="animalPicture" /> </li>)
                })
            }
        </ul>
    )
}

export default ShowArrayAsList;