import React from 'react';
import ShowArrayAsList from './ShowArrayAsList';


class Animal extends React.Component {


    //обязательная функция
    render() {

        let arrayAnimals = this.props.animal;
        return (
            <div>
                <h3>Animal Info</h3>

                <ShowArrayAsList arrayToDisplay = {arrayAnimals}/>

            </div>
        )
    }
}

export default Animal;